.second-flats {

    &__wrp {
        @media(max-width: 600px) {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
        }
    }

    &__bloc {
        &--floor {
            @media(max-width: 600px) {
                display: none;
            }
        }
    }

    &__pic {
        height: 400px;
        width: 100%;

        @media(max-width: 1024px) {
            height: 265px;
        }

        @media(max-width: 640px) {
            height: 194px;
        }

        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    &__box {
        display: flex;
        justify-content: space-between;
        margin-top: 24px;

        &:first-child {
            @media(max-width: 600px) {
                margin-top: 33px;
            }
        }

        &:last-child {
            margin-top: 38px;

            @media(max-width: 1024px) {
                margin-top: 25px;
            }

            @media(max-width: 992px) {
                flex-direction: column;
            }

            @media(max-width: 768px) {
                flex-direction: row;
            }

            @media(max-width: 600px) {
                margin-top: 19px;
            }
        }

        &--price {
            position: relative;
            z-index: 0;

            @media(max-width: 600px) {
                margin-top: 7px;
                padding: 9px 0 3px;
                flex-direction: column !important;
            }

            // &:after {
            //     @media(max-width: 600px) {
            //         position: absolute;
            //         content: "";
            //         height: 100%;
            //         background-color: $ochre-2;
            //         right: 0;
            //         top: 0;
            //         bottom: 0;
            //         left: -15px;
            //         width: calc(100% + 30px);
            //         z-index: -1;
            //     }
            // }
        }
    }

    &__adres {
        @media(max-width: 600px) {
            line-height: 26px;
        }
    }

    &__area {
        font-weight: bold;
        font-size: 20px;
        line-height: 26px;
        color: $black-1;
        letter-spacing: 0px;

        @media(max-width: 600px) {
            font-size: 25px;
            line-height: 30px;
            letter-spacing: 0.4px;
        }
    }

    &__num {
        font-weight: bold;
        font-size: 20px;
        line-height: 26px;
        color: $black-1;
        min-width: 65px;

        @media(max-width: 992px) {
            min-width: 70px;
        }

        @media(max-width: 600px) {
            display: none;
        }

        &--hide {
            display: none;

            @media(max-width: 600px) {
                display: block;
                min-width: 66px;
            }
        }
    }

    &__price {

        display: flex;

        @media(max-width: 600px) {
            position: relative;
            padding: 7px 0 0px;
        }

        &:after {
            @media(max-width: 600px) {
                position: absolute;
                content: "";
                height: 100%;
                background-color: $ochre-2;
                right: 0;
                top: 0;
                bottom: 0;
                left: -15px;
                width: calc(100% + 30px);
                z-index: -1;
            }
        }
    }

    &__adres {
        @media(max-width: 600px) {
            margin-top: 8px;
        }
    }

    &__unit {
        font-size: 16px;
        line-height: 26px;
        text-align: right;
        letter-spacing: 0.3px;

        @media(max-width: 600px) {
            font-size: 12px;
            line-height: 30px;
        }

        &--price {
            position: relative;
            font-weight: bold;
            font-size: 20px;
            line-height: 26px;
            padding-right: 12px;
            margin-right: 12px;
            letter-spacing: 0;

            @media(max-width: 600px) {
                padding-right: 10px;
                margin-right: 9px;
            }

            &:after {
                position: absolute;
                content: "";
                width: 1px;
                height: 20px;
                background-color: $black-1;
                right: 0;
                bottom: 6px;
                top: 0;
                margin: auto;

                @media(max-width: 600px) {
                    height: 15px;
                    bottom: 0;
                }
            }
        }
    }

    &__publish {
        letter-spacing: 0.3px;

        @media(max-width: 600px) {
            max-width: 155px;
            line-height: 27px;
        }

        span {
            font-weight: bold;
        }
    }

    &__items {
        max-height: 1856px;
        overflow: auto;

        @media(max-width: 768px) {
            max-height: initial;
            overflow: initial;
        }
    }

    &__item {
        display: block;
        padding-bottom: 17px;
        border-bottom: 0.5px solid $basic;

        @media(max-width: 600px) {
            padding-bottom: 22px;
        }

        &:not(:first-child) {
            margin-top: 70px;

            @media(max-width: 1024px) {
                margin-top: 45px;
            }

            @media(max-width: 600px) {
                margin-top: 40px;
            }
        }

        &:last-child {
            border-bottom: none;
            padding-bottom: 0;
        }
    }

    &--land {
        .second-flats__publish {
            display: flex;
            max-width: initial;
        }

        .second-flats__publish {
            span {
                margin-left: 5px;
            }
        }
    }
}