.slider-flats {
    opacity: 0;
    transition: all 0.3s linear 0s;

    @media(max-width: 500px) {
        display: flex;
    }

    &__box {
        display: flex;
        width: calc(100% - 63px - 42px);
    }



    &__arrow {
        display: flex;
        align-items: center;

        @media(max-width: 500px) {
            flex-direction: column;
            margin-top: 10px;
            padding-left: 32px;
        }

        .b-svg {
            display: block;
            height: 100%;
            width: 100%;
            fill: none;
            stroke: black;
            stroke-width: 2px;
        }

        &--next {
            transform: scale(-1, 1);

            @media(max-width: 500px) {
                transform: rotate(-90deg);
            }
        }

        &--next,
        &--prev {
            height: 13px;
            width: 13px;
            cursor: pointer;

            @media(max-width: 500px) {
                height: 17px;
                width: 17px;
            }
        }

        &--prev {
            @media(max-width: 500px) {
                transform: rotate(90deg);
            }
        }
    }

    &__left {
        width: 435px;
        height: 503px;
        margin-left: -24px;

        @media(max-width: 1300px) {
            width: 355px;
            height: 468px;
        }

        @media(max-width: 1235px) {
            width: 330px;
            height: 378px;
            margin-left: 0;
        }

        @media(max-width: 992px) {
            width: 240px;
            height: 278px;
        }

        @media(max-width: 600px) {
            width: 200px;
            height: 239px;
        }

        img {
            height: 100%;
            width: 100%;
            object-fit: contain;
        }
    }

    &__right {
        margin-left: -5px;
        padding-top: 13px;

        @media(max-width: 600px) {
            padding-top: 20px;
            transform: translateX(7px);
            margin-left: 0;
        }
    }

    &__ic {
        width: 88px;
        height: 88px;
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;

        @media(max-width: 1024px) {
            width: 77px;
            height: 77px;
        }

        @media(max-width: 992px) {
            width: 55px;
            height: 55px;
        }

        @media(max-width: 600px) {
            width: 50px;
            height: 50px;
        }
    }

    &__cover {
        display: flex;
        align-items: center;
        margin-top: 58px;
        z-index: 10;

        @media(max-width: 1300px) {
            margin-top: 25px;
        }

        @media(max-width: 768px) {
            margin-top: 10px;
        }

        @media(max-width: 500px) {
            margin-top: 10px;
            flex-direction: column;
            justify-content: flex-end;
            transform: translateX(-44px) translateY(-50px);
        }
    }

    &__text {
        margin-right: 24px;

        @media(max-width: 500px) {
            margin-right: 0;
        }
    }

    &__wrapp {
        display: flex;
    }

    &__pagination {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 43px;
        margin-right: 87px;
        justify-content: center;
        padding: 96px 0;

        @media(max-width: 1235px) {
            margin-right: 35px;
        }

        @media(max-width: 1024px) {
            margin-right: 20px;
        }

        @media(max-width: 992px) {
            padding: 50px 0;
        }

        @media(max-width: 600px) {
            padding: 30px 0;
            margin-right: 20px;
        }


        .slider-flats__bullet {
            cursor: pointer;
            padding: 0 9px;
            opacity: .3;

            &--active {
                opacity: 1;
            }

            @media(max-width: 500px) {
                padding: 0;
                line-height: 16px;
            }

            &:first-child {
                padding-left: 0;
            }

            &:last-child {
                padding-right: 0;
            }

            &:not(:last-child) {
                @media(max-width: 500px) {
                    margin-bottom: 10px;
                }
            }
        }

        span {
            display: block;
        }

        .mf-num {
            font-size: 40px;
            line-height: 40px;
            text-align: center;
            margin-bottom: 11px;

            @media(max-width:1024px) {
                font-size: 25px;
                line-height: 30px;
            }
        }

        .mf-txt {
            font-size: 16px;

            @media(max-width: 1024px) {
                font-size: 14px;
                line-height: 1.2;
            }
        }

        .swiper-button-disabled {
            opacity: 0;
        }
    }



    .arr {
        display: flex;
        align-items: center;
        transition: all 0.3s linear 0s;

        &:hover {
            .b-svg {
                stroke: $ochre;
            }
        }

        .b-svg {
            display: block;
            height: 100%;
            width: 100%;
            fill: none;
            stroke: black;
            stroke-width: 2px;
            transition: all 0.3s linear 0s;
        }

        &--next,
        &--prev {
            height: 13px;
            width: 13px;
            cursor: pointer;

            @media(max-width: 500px) {
                height: 17px;
                width: 17px;
            }
        }

        &--next {
            transform: rotate(90deg);
            margin-bottom: auto;
        }

        &--prev {
            transform: rotate(-90deg);
            margin-top: auto;
        }
    }
}